.bread_crumbs_section {
  position: relative;
  height: 330px;
  overflow: hidden; }

.bread_crumbs_bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background: #121212; }
  .bread_crumbs_bg img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.4; }

.service_navigation__block {
  position: relative;
  top: 0;
  padding: 0px;
  padding-top: 110px;
  min-height: 220px; }

.service_navigation {
  display: flex;
  align-items: center; }
  .service_navigation svg {
    transform: rotate(-90deg);
    width: 17px;
    height: 20px; }
  .service_navigation span {
    padding-left: 10px; }

.service_title {
  padding-top: 10px; }

.true_form {
  position: absolute;
  z-index: 9999;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  padding: 0;
  width: 500px;
  height: 250px;
  background: #fff !important;
  min-width: unset;
  text-align: center;
  min-height: unset; }
  .true_form .fancybox-button svg {
    display: none; }

.form_true_img {
  margin-top: 35px;
  width: 100px; }

.usually_modal_text {
  font-size: 16px;
  margin-top: 30px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal; }

.modal_form {
  background: #fff;
  padding: 20px 50px 50px 50px; }
  .modal_form .cons_text {
    font-size: 21px;
    font-weight: 900;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    letter-spacing: 1px;
    text-align: left;
    color: #302d2d; }
  .modal_form .label__style {
    font-size: 16px;
    font-weight: 400;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.13;
    letter-spacing: normal;
    color: #242321; }
  .modal_form .input_focus {
    border-bottom: 1.5px solid #242321 !important;
    color: #242321 !important; }

.post_forms {
  width: 525px;
  padding: 45px 60px;
  background-color: #2d2e28; }
  .post_forms input:-internal-autofill-selected {
    background-color: transparent !important; }
  .post_forms .inp-vak-wrap {
    margin-top: 35px;
    position: relative;
    height: 60px;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column; }
  .post_forms .label__style {
    transition: all .2s;
    bottom: 25px;
    position: absolute;
    font-size: 13px;
    letter-spacing: normal;
    color: #ffffff;
    padding: 5px; }
  .post_forms .inp-vak-wrap input {
    padding: 5px;
    outline: none; }
  .post_forms .input_focus {
    position: relative;
    z-index: 1;
    outline: none;
    border: none;
    background-color: transparent;
    font-size: 16px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.13;
    letter-spacing: normal;
    color: #ffffff !important; }
  .post_forms .inp-vak-wrap {
    position: relative;
    height: 55px; }
  .post_forms .label__style {
    transition: all .2s;
    bottom: 25px;
    position: absolute;
    font-size: 16px;
    z-index: 0; }
  .post_forms .label__style_active {
    transition: all .2s;
    bottom: 50px;
    font-size: 14px;
    color: #fff; }
  .post_forms .input_focus {
    position: relative;
    z-index: 1;
    outline: none;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #fff !important; }
  .post_forms .input_focus_active {
    border-bottom: 2px solid #fff !important; }
  .post_forms .inp-cons-wrap input {
    color: #00000096 !important;
    border-bottom: 1px solid #de4166; }
  .post_forms .submit_cons {
    outline: none;
    background: transparent;
    margin-top: 40px;
    display: block;
    border-radius: 5px;
    background-color: #c3d72b;
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 1px;
    text-align: center;
    color: #ffffff;
    text-align: center;
    padding: 17px 33px;
    border: none;
    cursor: pointer; }
  .post_forms .true_form {
    min-height: 260px; }
  .post_forms .input_focuss {
    background-color: #fff;
    border-bottom: 2px solid #de4166 !important; }
  .post_forms .cons_text {
    text-align: center;
    color: #c3d72b;
    line-height: 1;
    font-weight: 700;
    font-size: 15px;
    margin-bottom: 15px; }

.error {
  color: red;
  font-size: 13px;
  position: relative !important;
  padding-top: 3px; }

@media screen and (max-height: 830px) and (min-width: 1520px) {
  .service_navigation__block {
    position: relative; }
  .service_section {
    justify-content: unset; } }

@media (max-width: 992px) {
  .service_prof {
    width: 100%; }
  .service_section {
    overflow: auto;
    height: unset; }
  .service_navigation__block {
    position: relative; }
  .service_prof:after {
    content: '';
    display: none; } }
