.noUi-horizontal .noUi-handle {
  outline: none;
  width: 20px;
  height: 20px;
  right: -17px;
  top: -9px;
  border-radius: 50%;
  background: #2877b5;
  border: 1px solid #ccc;
  box-shadow: none; }

.noUi-target {
  border: 1px solid #ccc;
