.btn_standart {
  transition: all .2s;
  display: block;
  cursor: pointer;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: normal;
  font-weight: 800;
  font-size: 16px;
  letter-spacing: 1px;
  padding: 10px 25px;
  min-width: 170px;
  text-align: center;
  border-radius: 4px; }
  .btn_standart_white {
    color: #252525;
    border: solid 1px #fff;
    background: #fff; }
  .btn_standart_black {
    color: #fff;
    border: solid 1px transparent !important;
    background: #333333; }
    .btn_standart_black:hover {
      border: solid 1px #DB1A1A !important;
      background: #DB1A1A; }
    .btn_standart_black_2 {
      color: #595959;
      border: solid 1px #595959;
      background: transparent; }
      .btn_standart_black_2:hover {
        border: solid 1px #DB1A1A;
        background: #DB1A1A; }

@media (max-width: 435px) {
  .btn_standart {
    font-size: 15px;
    padding: 10px 10px; } }
