.header {
  display: flex;
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 90px;
  padding: 30px 0px;
  background-image: linear-gradient(180deg, #0a0b0b, rgba(24, 26, 26, 0)); }
  .header .container {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header_lang_link {
    transition: all .2s;
    border-bottom: 1px solid transparent;
    opacity: 0.3;
    padding-bottom: 3px;
    margin-right: 7px; }
    .header_lang_link.active {
      opacity: 1;
      border-bottom: 1px solid #fff; }
    .header_lang_link:hover {
      opacity: 1; }
  .header_item {
    width: 140px; }

@media (max-width: 440px) {
  .header_mobile_link {
    opacity: 0;
    pointer-events: none; } }
